.top-actions {
  --icon-width: 18px;
  display: grid;
  grid-template-columns: 1fr var(--icon-width) var(--icon-width);
  align-items: center;
  gap: 30px;

  @include onTablet {
    --icon-width: 24px;
    gap: 40px;
  }
  
  &__empty {
    height: 40px;
  }

  &__icon {
    grid-column: 3 / -1;
  }
}
