.menu {
  background-color: $color-light-blue;

  &__content {
    height: 100vh;
    padding: $header-top-padding 0;

    @include onSmallPhone {
      overflow: auto;
    }
  }

  &__top {
    margin-bottom: 55px;
  }
  &__list {
    list-style: none;
    margin-bottom: 40px;

  }

  &__item {
    margin-bottom: 24px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 8px;
    border-bottom: 1px solid transparent;
    font-weight: 700;
    font-size: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-decoration: none;
    color: $color-text;
    transition: border-color 0.3s;
    &:hover {
      color: rgb(107, 106, 106);
      border-color: $color-text;
    }
  }

  &__call {
    display: block;
    width: max-content;
    font-weight: 700;
    font-size: 12px;
    line-height: 16px;
    text-transform: uppercase;
    color: $color-text;
    text-decoration: none;

    &--phone {
      font-family: inherit;
      font-size: 16px;
      line-height: 27px;
      margin-bottom: 16px;
    }

    &--action {
      letter-spacing: 2px;
      padding-bottom: 8px;
      border-bottom: 1px solid $color-text;
    }
  }
}
