.contact {

  &__info-block {
    margin-bottom: 24px;
    color: $color-light-grey;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__text {
    display: block;
    text-decoration: none;
    color: #3e3e3e;
    margin-top: 8px;
    transition: color $transition-duration;

    &:hover {
      color: $color-light-grey;
      cursor: pointer;
    }

  }

  &__button {
    margin-bottom: 60px;

    @include onTablet {
      margin-bottom: 0;
    }
  }
}
